import React from 'react';
// @ts-ignore
import { navigate } from 'hookrouter';
import s from './Documentation.module.scss';
import Layout from '../../components/Layout';
import Heading from '../../components/Heading';
import Button from '../../components/Button';
import { LinkEnum } from '../../routes';

const Documentation = () => {
  return (
    <div className={s.root}>
      <Layout className={s.contentWrap}>
        <div className={s.contentText}>
          <Heading className={s.heading} headingSize={1}>
            Some about pokemons
          </Heading>

          <p>
            The legendary world of Pokmon first reached Australian and New Zealand shores in 1998 with Pokmon Red
            Version and Pokmon Blue Version for Game Boy, becoming an instant sensation that transcended fans of all
            ages and backgrounds.
          </p>
          <p>
            Since the 1996 debut of Pokmon Red and Pokmon Green for Game Boy in Japan, this iconic series has sold
            more than 279 million video games globally, with avid Aussie and Kiwi Pokmon fans continuing to grow their
            Pokmon collections.
          </p>

          <Heading className={s.heading} headingSize={2}>
            What are Pokmon?
          </Heading>

          <p>
            Pokmon are creatures of all shapes and sizes who live in the wild or alongside humans. For the most part,
            Pokmon do not speak except to utter their names. There are currently more than 700 creatures that inhabit
            the Pokmon universe.
          </p>
          <p>
            Pokmon are raised and commanded by their owners (called Trainers). During their adventures, Pokmon grow,
            level up and become more experienced and even, on occasion, evolve into stronger Pokmon.
          </p>
          <p>
            There are over a dozen different types of Pokmon, such as Fire type, Psychic type, and Dragon type. Every
            Pokmon type comes with both advantages as well as disadvantages when battling different Pokmon types.
          </p>
          <p>
            For instance, a Fire type Pokmon has the edge against Grass type Pokmon, but will be at a disadvantage
            against Water type Pokmon. This makes strategic thinking, positioning and use of the Pokmon in your team a
            crucial part of battles.
          </p>
          <Button onClick={(event) => navigate(LinkEnum.POKEDEX)}>See pokemons</Button>
        </div>
      </Layout>
    </div>
  );
};

export default Documentation;
